import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import SwipeableDrawer from '@material-ui/core/SwipeableDrawer';
import Button from '@material-ui/core/Button';
import List from '@material-ui/core/List';
import Divider from '@material-ui/core/Divider';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import RemoveIcon from '@material-ui/icons/RemoveCircle';
import { tsPropertySignature } from '@babel/types';
import Badge from '@material-ui/core/Badge';

const useStyles = makeStyles({
  list: {
    width: 250,
  },
  fullList: {
    width: 'auto',
  },
});
const bats=[
    {
        quantity:3,
        model:"cool bat",
        color: "blue",
        size: '34"',
        engraving:"none",
        
    
    },
    {
        quantity:1,
        model:"cooler bat",
        color: "red",
        size: '34"',
        engraving:"none",
        
    
    }
]
function SwipeableTemporaryDrawer(props) {
    
  const classes = useStyles();
  const [state, setState] = React.useState({
    top: false,

  });

  const toggleDrawer = (side, open) => event => {
    if (event && event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }

    setState({ ...state, [side]: open });

  };



  const fullList = side => (
    <div
      className={classes.fullList}
      role="presentation"
      
    >
      <List>
        {props.bats.map((bat, index) => (
          <ListItem button key={index}>
            <ListItemIcon onClick={()=>props.removeItem(index)}><RemoveIcon/></ListItemIcon>
            <ListItemText primary={`${bat.model} (x${bat.quantity})`}  />
          </ListItem>
        ))}
        <ListItem onClick={()=>props.emptyCart()} button>
            <ListItemIcon><RemoveIcon/></ListItemIcon>
            <ListItemText primary="Empty Your Cart"  />
          </ListItem>
      </List>
      <Divider />
      <List>
        
          <ListItem button>
            <ListItemIcon><InboxIcon /></ListItemIcon>
            <ListItemText primary={"Checkout"} 
            onClick={toggleDrawer(side, false)}
            onKeyDown={toggleDrawer(side, false)}/>
          </ListItem>
        
      </List>
    </div>
  );

  return (
    <div>
      
      <Button onClick={toggleDrawer('top', true)}><Badge className={props.magicClass} badgeContent={props.badgeContent} color={props.color}>
          {props.icon}
        </Badge></Button>
      
      <SwipeableDrawer
        anchor="top"
        open={state.top}
        onClose={toggleDrawer('top', false)}
        onOpen={toggleDrawer('top', true)}
      >
        {fullList('top')}
      </SwipeableDrawer>
      
    </div>
  );
}

export default SwipeableTemporaryDrawer;